import * as React from "react";
import { BarChart } from "@mui/x-charts/BarChart";
import { useState, useEffect } from "react";
import { useAuthContext } from "../../hooks/useAuthContext";

export default function SimpleCharts() {
  const [members, setMembers] = useState("");
  const [centers, setCenters] = useState("");
  const [membercount, setMembercount] = useState("");
  const [trainercount, setTrainercount] = useState("");
  const [transactioncount, setTransactioncount] = useState("");
  const [expiredMembersCount, setExpiredMembersCount] = useState(0);
  const { user } = useAuthContext();
  useEffect(() => {
    const fetchMembers = async () => {
      const response = await fetch("/api/member/");
      const json = await response.json();

      if (response.ok) {
        setMembers(json);
        setMembercount(json.length);
        setTransactioncount(json.length);
      }
      const currentDate = new Date();

      // Filter members with expiredate > today's date
      const filteredMembers = json
        ? json.filter((Member) => currentDate > new Date(Member.expiredate))
        : [];
      setExpiredMembersCount(filteredMembers.length);
    };
    const fetchTrainers = async () => {
      const response = await fetch("/api/trainer/");
      const json = await response.json();

      if (response.ok) {
        setTrainercount(json.length);
      }
    };
    const fetchCenters = async () => {
      const response = await fetch("/api/center/", {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      });
      const json = await response.json();

      if (response.ok) {
        setCenters(json.length);
      }
    };

    if (user) {
      fetchMembers();
      fetchTrainers();
      fetchCenters();
    }
  }, []);
  return (
    <BarChart
      series={[
        { data: [centers], stack: "A", label: "Center",color: "#ffc100"},
        { data: [membercount], stack: "B", label: "Member" ,color:"#ff9a00"},
        { data: [trainercount], stack: "C", label: "Trainer", color: "#ff7400" },
        { data: [transactioncount], stack: "D", label: "Transcations", color: "#ff4d00" },
        { data: [expiredMembersCount], stack: "E", label: "Expired",color: "#ff0000" },
      ]}
      height={450}
      
     
    />
  );
}
